.popup {
  background-color: rgba(84, 84, 84, 0.35);
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  &.fade-in {
    animation: fadeIn .35s ease;
  }
  &.fade-out {
    animation: fadeOut .35s ease;
  }
  &.active {
    display: block;
  }
  &__inner {
    padding: 60px 50px 42px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    max-width: 500px;
    width: 100%;
    background-color: #ffffff;
    border-radius: 10px;
    // outline: 1px solid red;
  }
  &__title {
    font-size: 40px;
    text-align: center;
    margin-bottom: 50px;
  }
  &__form {}
  &__policy {
    font-size: 13px;
    color: #D0D0D0;
    text-align: center;
    max-width: 300px;
    margin: 0 auto;
  }
}
.request-form {
  margin-bottom: 10px;
  &__input {
    padding: 14px 30px;
    margin-bottom: 30px;
    font-size: 18px;
    border: 1.5px solid #D0D0D0;
    border-radius: 5px;
    max-width: none;
    width: 100%;
    &::placeholder {
      font-size: 18px;
      line-height: 1.2;
      color: rgb(208, 208, 208);
      transition: .25s;
    }
    &:focus::placeholder {
      color: rgba(208, 208, 208, 0);
    }
  }
  &__btn {
    margin: 0 auto;
    display: block;
  }
}

@keyframes fadeIn {
  from {opacity: 0;}
  to {opacity: 1;}
}
@keyframes fadeOut {
  from {opacity: 1;}
  to {opacity: 0;}
}

@media (max-width: 900px) {
  .popup {
    &__title {
      font-size: 30px;
      margin-bottom: 35px;
    }
  }
  .request-form {
    &__input {
      margin-bottom: 20px;
    }
  }
}
@media (max-width: 767px) {

}
@media (max-width: 600px) {
  .popup {
    &__inner {
      width: calc(100% - 40px);

    }
  }
}

@media (max-width: 480px) {
  .popup {
    &__inner {
      padding-left: 10px;
      padding-right: 10px;
    }
    &__title {
      // white-space: nowrap;
      // margin-bottom: 15px;
      font-size: 25px;
    }

  }
  .request-form {
    &__input {
      padding: 10px 20px;
      font-size: 16px;
      &::placeholder {
        font-size: 16px;
      }
    }
    &__btn {
      font-size: 20px;
    }
  }
}